.sign-up-page-wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .form-container{
    background: #FFFFFF;
    box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.05);
    border-radius: 16px;
    margin-top: 3%;

    .form-header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

    .top-icon{
    width: 79.91px;
    height: 82px;
    align-self: center;
    margin: 29px 0 24px 0;
    }

    .main-title{
    margin-top: 0;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    color: #5B5B5B;
    }
    
    
    

    }

    }

    .gender-container{
        display: flex;
        flex-direction: column;
        margin: 35px 35px 30px 35px;
    }

