    //     font-style: normal;
    //     font-weight: bold;
    //     font-size: 18px;
    //     line-height: 22px;
    //     color: #5B5B5B;
    //    margin: 0 0 12px 35px;
    // }
}

.gender-boxes-wrapper{
    display: flex;
flex-direction: row;
justify-content: space-between;
}

.inputs-container{
    

    .input-wrapper{
        display: flex;
        flex-direction: column;
        
        .secondary-title{
            font-family: Montserrat;
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 22px;
            color: #5B5B5B;
           margin: 0 0 12px 35px;
        }
        
            .input-item{
                width: 500px;
                height: 70px;
                border: 1px solid #C1C1C1;
                box-sizing: border-box;
                border-radius: 16px;
                margin-left: 35px;
                position: relative;
                z-index: 3;
            }

            .input-icon{
            width: 36.12px;
            height: 26px; 
            position: relative;
            z-index: -1;
            top: 220%;
            left: 40%;
            }
}


}
